// Frontend Only
.theme-wecodeart {
    // Align
    .alignnone {
        margin: 0 0 var(--wp--custom--spacers--3) 0;
    }
    
    .aligncenter {
        display: block;
        margin: 0 auto var(--wp--custom--spacers--3);
    }
    
    .alignright {
        float: right;
        margin: 0 0 var(--wp--custom--spacers--3) var(--wp--custom--spacers--3);
    }
    
    .alignleft {
        float: left;
        margin: 0 var(--wp--custom--spacers--3) var(--wp--custom--spacers--3) 0;
    }

    // Background Dim
    .has-background-dim {
        opacity: 0.5;
        z-index: 1;

        @for $i from 0 through 10 {
            &-#{ $i * 10 } {
                opacity: $i * 0.1;
            }
        }
    }

    // Transitions
    .fade {
        @include transition($transition-fade);

        &:not(.show) {
            opacity: 0;
        }
    }

    .collapse {
        &:not(.show) {
            display: none;
        }
    }

    .collapsing {
        height: 0;
        overflow: hidden;
        @include transition($transition-collapse);

        &.collapse-horizontal {
            width: 0;
            height: auto;
            @include transition($transition-collapse-width);
        }
    }

    // SVG
    svg {
		display: inline-block;
		vertical-align: -0.125em;
		fill: currentColor;
		height: 1em;
	}

    svg,
    .svg-inline {
        .fa-secondary {
            opacity: .4;
        }

        &.fa-fw {
            width: 1em;
        }
    }
}

// Text
.has-text-align-left {
    text-align: left;
}

.has-text-align-center {
    text-align: center;
}

.has-text-align-right {
    text-align: right;
}

.has-text-align-justify {
    text-align: justify;
}

.has-underline {
    text-decoration: underline;
}

.text-truncate {
    @include text-truncate();
}

@for $i from 1 through 6 {
	.display-#{$i} {
		font-size: var(--wp--custom--display--#{$i});
		font-weight: 300;
		line-height: 1.2;
	}
}

.list-unstyled {
	@include list-unstyled();
}

.list-inline {
	@include list-unstyled();
}

.list-inline-item {
	display: inline-block;

	&:not(:last-child) {
		margin-right: 1rem;
	}
}

.small {
	@extend small;
}

.mark {
	@extend mark;
}

.initialism {
	font-size: var(--wp--preset--font-size--small);
	text-transform: uppercase;
}

// Clearfix
.clearfix {
    @include clearfix();
}

// Visually hidden
.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
    @include visually-hidden();
}

.screen-reader-response,
.screen-reader-text {
    position: absolute;
    background-color: var(--wp--preset--color--light);
    color: var(--wp--preset--color--dark);
    padding: .5rem 1rem;
    top: 5px;
    left: 5px;
    z-index: 100000;

    @include visually-hidden-focusable();
}

// Ratio
.ratio {
    position: relative;
    width: 100%;
  
    &::before {
        display: block;
        padding-top: var(--#{$variable-prefix}aspect-ratio);
        content: "";
    }
  
    > * {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
  
@each $key, $ratio in $aspect-ratios {
    .ratio--#{$key} {
        --#{$variable-prefix}aspect-ratio: #{$ratio};
    }
}

// Position
.fixed-top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
}
  
.fixed-bottom {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1030;
}
  
// Responsive sticky top
@each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
        $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

        .sticky#{$infix}-top {
            position: sticky;
            top: 0;
            z-index: 1020;
        }
    }
}

// Stacks
.hstack {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-self: stretch;
}
  
.vstack {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    align-self: stretch;
}

// Vertical Line
.vr {
    display: inline-block;
    align-self: stretch;
    background-color: currentColor;
    width: 1px;
    min-height: 1em;
    opacity: .25;
}